.bux-container {
  width: 100%;
  max-width: 1440px;
  padding-right: 32px;
  padding-left: 32px;
  margin-right: auto !important;
  margin-left: auto !important;
}
@media (min-width: 640px) {
  .bux-container {
    padding-right: 64px;
    padding-left: 64px;
  }
}
@media (min-width: 960px) {
  .bux-container {
    padding-right: 128px;
    padding-left: 128px;
  }
}

