.bux-grid {
  display: flex;
  flex-wrap: wrap;
  clear: both;
  margin-right: -16px;
  margin-left: -16px;
}

.bux-grid:after {
  display: block;
  height: 0;
  clear: both;
  content: "";
  visibility: hidden;
}

.bux-grid--no-gutters {
  margin-right: 0;
  margin-left: 0;
}

.bux-grid__cell {
  flex-basis: 0;
  flex-grow: 1;
  align-self: stretch;
  width: 100%;
  min-height: 1px;
  float: left;
  margin-right: 16px;
  margin-left: 16px;
}

